import * as React from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';

const usertype = [
  {
    value: 'customer',
    label: 'Customer'
  },
  {
    value: 'vendor',
    label: 'Vendor'
  },
  {
    value: 'staff',
    label: 'Staff'
  }
];

export default function VisiotType() {
  const [currency, setCurrency] = React.useState('customer');

  const handleChange = (event) => {
    setCurrency(event.target.value);
  };

  return (
    <Box
      component="form"
      sx={{
        '& .MuiTextField-root': { m: 1, width: '500px' }
      }}
      noValidate
      autoComplete="off"
    >
      <TextField id="outlined-select-currency" select label="Visiot Type *" value={currency} onChange={handleChange}>
        {usertype.map((option) => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    </Box>
  );
}
